<template>
    <section>
        <div class="modal my-popups  fade show" role="dialog" style="display: block;">
            <div class="modal-dialog mw-1100" role="document">
                <div class="modal-content">
                    <div class="modal-header mheader-user">
                        <button type="button" class="close" @click="$emit('close')"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                        <h4 id="myModalLabel" class="modal-title">{{ $t(reqFrom) }}</h4>
                    </div>
                    <div class="modal-body">
                        <dynamic-form v-if="isLoaded" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory"> {{$t('MandatoryString')}} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataServices from "../../../services/DataService";
export default {
    props:{
        reqFrom:  {
            type: String,
            required: true
        },
    },
    data(){
        return{
            isLoaded:false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "DateField",
                            label: "Start Date (M/D/YYYY)",
                            name: "StartDate",
                            value: "",
                            placeholder: "",
                            config: {
                                format: "M/D/YYYY",
                                minDate:new Date()
                            },
                            validationRules: {"required": true},
                            visibility: true,
                        },
                        {
                            astype: "DateField",
                            label: this.$t('StartTime'),
                            name: "StartTime",
                            value: "",
                            placeholder: "",
                            config: {
                                mode: 'Time',
                                minDate:new Date()
                            },
                            validationRules: {"required": true},
                            visibility: true,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Approver'),
                            name: "Approver",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            validationRules: {"required": true},
                            visibility: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('AddNote'),
                            name: "AddNote",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            visibility: false,
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: this.reqFrom=='SchedulePost' ? "Schedule Post" :'<i class="fa fa-clock-o pr-2"></i>Schedule & Send For Approval'
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                },
            ],
        }
    },
    async created(){
        var vm = this;
        if(vm.reqFrom=='SendForApproval'){
            vm.FormSchema[0].Data[2].visibility=true;
            vm.FormSchema[0].Data[3].visibility=true;
            await vm.GetCampaignApprovers();
        }
        vm.isLoaded=true;
    },
    methods:{
        onSubmit: function (formfields, values) {
            var vm = this;
            vm.$parent.scheduleDate=formfields.StartDate;
            vm.$parent.scheduleTime=formfields.StartTime;
            vm.$parent.approverId=formfields.Approver;
            vm.$parent.approverNote=formfields.AddNote;
            vm.$parent.saveForm(vm.reqFrom);
            vm.$emit('close');

        },
        Close(){
            var vm =this;
            vm.$emit('close');
        },
        async GetCampaignApprovers() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_USERS";
            await DataServices.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var Users = response.data.DATA;
                    var campDDLData = [];
                    if (Users != null) {
                        Users.forEach(x => {
                            campDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[0].Data[2].config.options = Users;
                    }
                }
            });
        },
    }
}
</script>