<template>
    <section>
        <div class="container-fluid mt-3 tab-style plaintabs">



<div class="col-md-12 p-0 mt-2">
    <div class="theme-primary partition-full">
        <span class="p-name text-white">{{$t('NewSocialPost')}}</span>
        <span class="p-actions float-right">
            <a href="javascript:;" value="cal" id="ShowCalender" class="p-action-btn text-white" title="" data-toggle-tooltip="tooltip" data-original-title="Back to List"><i class="fa fa-angle-double-left pr-2"></i><br /><span> Back to List</span></a>
        </span>
    </div>

</div>

<div class="row">
    <div class="col-md-12 p-0 mt-2 float-left">
        <div class="col-sm-12 col-md-6 p-0 float-left">
            <Form ref="AddPost">
            <dynamic-form ref="AddPostForm" lang="en" :buttons="buttons" :schema="FormSchema"> <!-- v-on:OnSubmit="onSubmit"-->
                <template v-slot:tgslot-Content="{ data }">
                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"></ckeditor> <!--:config="editorConfig"-->
                    <span name="Content" class="invalid-feedback d-block">{{ data.error }}</span>
                </template>
            </dynamic-form>
            <div v-if="isShowAdditionalBtns" class="col-12 float-left mt-3">
                <div class="float-left border" style="max-width:450px; min-width:450px;">

                    <a href="javascript:;" v-on:click="saveForm('PostNow')" class="float-left d-flex p-2 borderbottom w-100">
                        <div class="float-left d-block">
                            <em class="fa fa-paper-plane text-info p-3 font-24"></em>
                        </div>
                        <div class="float-left mt-2 ml-2">
                            <span class="w-100 float-left font-weight-medium">Post Now</span>
                            <span class="w-100 float-left font-12 font-weight-medium">Send out this post to the selected social channels/accounts.</span>
                        </div>
                    </a>

                    <a href="javascript:;" v-on:click="SchedulePost('SchedulePost')" class="float-left d-flex p-2 borderbottom w-100">
                        <div class="float-left d-block">
                            <em class="fa fa-clock-o p-3 font-24" style="color:#1dbdac"></em>
                        </div>
                        <div class="float-left mt-2 ml-2">
                            <span class="w-100 float-left font-weight-medium">Schedule Post</span>
                            <span class="w-100 float-left font-12 font-weight-medium">Schedule a post for a later date or time.</span>
                        </div>
                    </a>

                    <a href="javascript:;" v-on:click="SchedulePost('SendForApproval')" class="float-left d-flex p-2 borderbottom w-100">
                        <div class="float-left d-block">
                            <em class="fa fa-check-circle text-success p-3 font-24"></em>
                        </div>
                        <div class="float-left mt-2 ml-2">
                            <span class="w-100 float-left font-weight-medium">Send Post For Approval</span>
                            <span class="w-100 float-left font-12 font-weight-medium">Send for approval first with in your agency or organisation.</span>
                        </div>
                    </a>

                    <a href="javascript:;" class="float-left d-flex p-2 w-100">
                        <div class="float-left d-block">
                            <em class="fa fa-trash text-danger p-3 font-24"></em>
                        </div>
                        <div class="float-left mt-2 ml-2">
                            <span class="w-100 float-left font-weight-medium">Delete</span>
                            <span class="w-100 float-left font-12 font-weight-medium">Delete this post forever.</span>
                        </div>
                    </a>

                </div>
            </div>
        </Form>
        </div>

        <div class="col-sm-12 col-md-6 p-0 float-left bg-light p-4">

            <div class="col-12 bg-white p-2">
                <div class="col-12 bg-light p-2">Preview in</div>

                <div class="col-12 bg-light p-2 mt-2">
                    <span v-html="FormSchema[1].Data[0].value"></span>
                    <img v-for="imgSrc in fileData" v-bind:src="'data:image/gif;base64,'+ imgSrc.base64String" />
                </div>
            </div>
        </div>
    </div>
</div>
<modalScheduleAndApprovePost v-if="isShowScheduleApproveModal" :reqFrom="reqFrom" @close="isShowScheduleApproveModal=false"></modalScheduleAndApprovePost>
</div>
    </section>
</template>

<script>
import DataService from '../../../../services/DataService';
import UploadAdapter from  '../../../../assets/common/CKEditorFileRendor';
import modalScheduleAndApprovePost from '../../../../components/Common/Modal/modalScheduleAndApprovePost.vue';
export default{
    components:{
        modalScheduleAndApprovePost
    },
    data(){
        return{
            editor: this.CLASSIC_EDITOR,
            AllPages:[],
            content: "<p>Some initial content</p>",
            editorConfig: {
                extraPlugins: [uploader],
            },
            File:null,
            fileData:[],
            schedule:[],
            approverData: [],
            base64String:null,
            isShowAdditionalBtns:false,
            isShowScheduleApproveModal:false,
            reqFrom:'',
            scheduleDate:null,
            scheduleTime:null,
            approverId:0,
            approverNote:'',
            ContainerName: null,
            UserId: null,
            ModuleName: 'CRM',
            SubModuleCode: 'CRM_POST_SENT',
            FormSchema:[
                {
                    layoutType: "double",
                    Data:[
                        {
                            astype: "SelectField",
                            label: this.$t('PostTo'),
                            name: "PostTo",
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options:[]
                            },
                            validationRules: { "required": true }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('AssignCampaignOwner'),
                            name: 'AssignCampaignOwner',
                            value: "",
                            placeholder: this.$t('Select'),
                            config: {
                                options:[]
                            },
                            validationRules: { "required": false }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data:[
                        {
                            astype: "SlotField",
                            label: this.$t('Content'),
                            name: "Content",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true }
                        },
                        {
                            astype: "FileField",
                            showLabel: true,
                            label: this.$t('Upload Imgae/Video'),
                            name: "Attachment",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 1,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                onChange: this.GetBase64String,
                                showAddIcon: false,
                                onAddButtonClick: "",
                                multiple: false
                            },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('FollowUpComment'),
                            name: "FollowUpComment",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                                //onChange: this.onChangeProfile,
                                showAddIcon: true,
                                //onAddButtonClick: this.AddSMTPCredentials,
                            },
                            validationRules: { "required": false },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Tags'),
                            name: "Tags",
                            value: "",
                            placeholder: "Select",
                            config: {
                                options: [],
                            },
                            validationRules: { "required": false },
                        }
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save for Later",
                    value:"Draft",
                    onClick:this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-primary",
                    text: "Post<i class='fa fa-angle-double-right pl-2 pr-2'></i>",
                    onClick: this.hideShowBtns
                },
            ],
        }
    },
    async created(){
        var vm =this;
        await vm.GetPageList();
        vm.CompanyId= vm.GetUserInfo.CompanyId;
        vm.ContainerName = vm.GetUserInfo.ContainerName;
        vm.UserId = vm.GetUserInfo.ID;
        await vm.GetCampaignOwners();
        await vm.GetTags();
        await vm.GetCategories();
        vm.FormSchema[1].Data[0].value=vm.content;
        vm.$refs.AddPostForm.UpdateKeyValue();
    },
    methods:{
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
            var divToRemove = document.querySelector('.ck-file-dialog-button');
            divToRemove.remove(); //for image remove
            let mediaIcon=document.getElementsByClassName('ck-dropdown')[2]
            mediaIcon.remove();   // for video icon remove
        },
       saveForm(type){
            var vm = this;
            if(typeof(type)=='object'){
                type=type.value;
            }
            let isDraft =false;
            if(type=='Draft'){
                isDraft=true;
            }
            vm.$refs.AddPost.validate().then(async result => {
                if(result){
                    var url ='';
                    vm.schedule=[];
                    vm.approverData=[];
                    var CustomFieldArray = vm.$refs.AddPostForm.ConvertDynamicFormObjToCustomFieldObj(vm.FormSchema);
                    if(type=="SchedulePost"){
                    var ScheduleObj = {
                    start_date:vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                    start_time:vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,
                    }
                    vm.schedule.push(ScheduleObj);
                    }
                    if(type=="SendForApproval"){
                    var approverObj = {
                    start_date:vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                    start_time:vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,
                    approver_id: vm.approverId,
                    note: vm.approverNote,
                    }
                    vm.approverData.push(approverObj);
                    }
                    
                    var obj = {
                        ref_page_id:CustomFieldArray.filter(x=>x.field_name=='PostTo')[0].field_value,
                        campaign_owner_id:CustomFieldArray.filter(x=>x.field_name=='AssignCampaignOwner')[0].field_value,
                        // attacthment:CustomFieldArray.filter(x=>x.field_name=='Attachment')[0].field_value,
                        content:CustomFieldArray.filter(x=>x.field_name=='Content')[0].field_value,
                        followup_content:CustomFieldArray.filter(x=>x.field_name=='FollowUpComment')[0].field_value,
                        category_id:CustomFieldArray.filter(x=>x.field_name=='Category')[0].field_value,
                        tag_id:CustomFieldArray.filter(x=>x.field_name=='Tags')[0].field_value,
                        is_draft : isDraft,
                        schedule_date:vm.scheduleDate ? vm.$options.filters.formatDate(vm.scheduleDate) : vm.scheduleDate,
                        schedule_time:vm.scheduleTime ? vm.$options.filters.formatTime(vm.scheduleTime) : vm.scheduleTime,           
                        schedule:vm.schedule,
                        approver: vm.approverData,
                        attachments: vm.fileData
                    }
                    url =JSON.stringify(obj);
                     
                    // vm.postModel.pageId=CustomFieldArray.filter(x=>x.field_name=='PostTo')[0].field_value;
                    // vm.postModel.campaign_owner=CustomFieldArray.filter(x=>x.field_name=='AssignCampaignOwner')[0].field_value;
                    // vm.postModel.attacthment=vm.fileData;
                    // vm.postModel.content=CustomFieldArray.filter(x=>x.field_name=='Content')[0].field_value;
                    // vm.postModel.follow_up_comments=CustomFieldArray.filter(x=>x.field_name=='FollowUpComment')[0].field_value;
                    // vm.postModel.category=CustomFieldArray.filter(x=>x.field_name=='Category')[0].field_value,
                    // vm.postModel.tags=CustomFieldArray.filter(x=>x.field_name=='Tags')[0].field_value;
                    // vm.postModel.is_draft=isDraft;
                    
                    // const formdata = new FormData();
                    // formdata.append("postString", JSON.stringify(vm.postModel));
                    // formdata.append("file", vm.fileData); 
                    let accountForPost = vm.AllPages.filter(x=>x.PAGE_ID==obj.ref_page_id)[0].ACCOUNT_NAME;
                    if(accountForPost && !isDraft && type!="SendForApproval"){
                        if(accountForPost.toLowerCase()=='facebook'){
                           await DataService.UploadFacebookPost(url).then(function(response){
                            })
                        }else if(accountForPost.toLowerCase()=='instagram'){

                        }
                    }
                    vm.scheduleDate=null;
                    vm.scheduleTime=null;
                    await vm.SaveSocialMediaPost(url);
                }
            })
        },
        uploadAdapterPlugin(editor) {
            var vm=this;
            editor.plugins.get('FileRepository').createUploadAdapter = (loader) => {
            return loader.file.then((file) => {
                vm.File =file;
            })

        };
        },
        async GetPageList(){
            var vm=this;
            var url = 'TYPE=CRM_SOCIAL&MODULE_NAME=CRM';
            DataService.GetTriggerFieldValue(url).then(function (response) {
                vm.AllPages = response.data;
                if(vm.AllPages !=null && vm.AllPages !=undefined){
                    vm.FormSchema[0].Data[0].config.options=[];
                    vm.AllPages.forEach(item=>{
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: item.NAME,
                            value: item.PAGE_ID
                        })
                    })
                }
            })
        },
        async GetCampaignOwners() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_USERS";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var CampaignOwnerData = response.data.DATA;
                    var campDDLData = [];
                    if (CampaignOwnerData != null) {
                        vm.FormSchema[0].Data[1].config.options=[];
                        CampaignOwnerData.forEach(x => {
                            campDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[0].Data[1].config.options = campDDLData;
                    }
                }
            });
        },
        async GetTags() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_TAGS";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var TagData = response.data.DATA;
                    var tagDDLData = [];
                    if (TagData != null) {
                        vm.FormSchema[1].Data[4].config.options=[];
                        TagData.forEach(x => {
                            tagDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[1].Data[4].config.options = tagDDLData;
                    }
                }
            });
        },
        async GetCategories() {
            var vm = this;
            var url = "id=&moduleName=CRM&type=CRM_CAMPAIGN_CATEGORY";
            await DataService.getDDLData(url + "&search=").then(response => {
                if (response.data != '') {
                    var Categories = response.data.DATA;
                    var catDDLData = [];
                    if (Categories != null) {
                        vm.FormSchema[1].Data[3].config.options=[];
                        Categories.forEach(x => {
                            catDDLData.push({
                                name: x.name,
                                value: x.value
                            })
                        })
                        vm.FormSchema[1].Data[3].config.options = catDDLData;
                    }
                }
            });
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                vm.fileData = [];

                var files = evt.target.files; // FileList object
                for (var i = 0; i < files.length; i++) {
                    var f = files[i];
                    var reader = new FileReader();
                    // Closure to capture the file information.
                    reader.onload = (function (theFile) {
                        return function (e) {
                            var binaryData = e.target.result;
                            //Converting Binary Data to base 64
                            var base64StringEncoded = window.btoa(binaryData);
                            vm.base64String=base64StringEncoded;
                            //showing file converted to base64

                            var fileName = f.name;
                            vm.fileName = fileName;
                            var n = fileName.lastIndexOf(".");
                            var extensionName = fileName.substring(n + 1, fileName.length);

                            vm.fileExt += "." + extensionName;
                            var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                            var fileSize = f.size;
                            vm.fileSize = fileSize;
                            var fileObj = {
                                attachement_mime: attachemnetMime,
                                fileName: fileName,
                                userId: vm.UserId,
                                companyId: vm.CompanyId,
                                id: 0,
                                moduleName: vm.ModuleName,
                                subModuleCode: vm.SubModuleCode,
                                containerName: vm.ContainerName,
                                base64String: base64StringEncoded,
                                attachement_path:''
                            };

                            vm.fileData.push(fileObj);
                        };
                    })(f);
                    // Read in the image file as a data URL.
                    reader.readAsBinaryString(f);
                }
            }
            catch (ex) {
            }
        },
        hideShowBtns(){
            var vm=this;
            vm.isShowAdditionalBtns= vm.isShowAdditionalBtns ? false : true;
        },
        async SaveSocialMediaPost(url){
            var vm =this;
           await DataService.SaveSocialPost(url).then(async function(response){
            if(response.data.MAIN_ID>0){
                    if(vm.fileData.length > 0){
                        vm.fileData.forEach(x=>{
                            x.id = response.data.MAIN_ID;
                        })
                    }
                    var Notificationobj = {
                        message: "Test^"+vm.GetUserInfo.Name,
                        notificationFor: vm.approverId,
                        typeCode: "CRM_APPROVAL_RAISED_BY",
                        referenceId: 0,
                        referenceId2: 0
                    }
                    await DataService.InsertNotification(JSON.stringify(Notificationobj)).then(function (response) {
                        //for Insert the notification 
                    })
                    var formData = JSON.stringify(vm.fileData);
                   await DataService.UploadFile(formData).then(function (response) {
                    })
                    vm.$router.push({ name: 'CRMCampaign' });
                }
            })
        },
        SchedulePost(reqFrom){
            var vm =this;
            vm.reqFrom=reqFrom;
            vm.isShowScheduleApproveModal = true;
        }
    }
}
export const uploader = function (editor)
{
    editor.plugins.get('FileRepository').createUploadAdapter = (loader) =>
        new UploadAdapter(loader);
};

</script>
